import React from 'react';
import logo from './logo.svg';
import c from './cof.png'
import '@vkontakte/vkui/dist/vkui.css';
import {Root, View, Panel, File} from '@vkontakte/vkui'
import * as mobilenet from '@tensorflow-models/mobilenet';
import './App.css';

var preimg = null;
async function  W(){
  var preimg = document.getElementById('preimg');
  var img = new Image(); // Конструктор HTML5
  img.src = preimg.value;


// Load the model.
const model = await mobilenet.load();

// Classify the image.
const predictions = await model.classify(img);

console.log('Predictions: ');
console.log(predictions);
}

function App() {
  return (
    <View activePanel={'img'}>
     <Panel id="img">
       <File id="preimg" onChange={()=>W()} />
       <img src={preimg} id="img" />
     </Panel>
    </View>
  );
}

export default App;
